/* -- common -- */
.anibyvue,
.anibyvue-mask {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}
.anibyvue {
    position: fixed;
}
.anibyvue-mask {
    position: absolute;
    background: rgba(0, 0, 0, .3);
}
.anibyvue-dialog {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 101;
    padding: 15px;
    background: #fff;
    border-radius: 3px;
    -webkit-box-shadow: 0 1px 3px rgba(0, 0, 0, .2);
            box-shadow: 0 1px 3px rgba(0, 0, 0, .2);
}
.anibyvue-close {
    position: absolute;
    cursor: pointer;
    top: 16px;
    right: 16px;
    width: 16px;
    height: 16px;
    z-index: 9999;
}
.anibyvue-close:before,
.anibyvue-close:after {
    position: absolute;
    content: '';
    height: 2px;
    width: 100%;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: #999;
    border-radius: 100%;
    -webkit-transition: background .2s;
    transition: background .2s;
}
.anibyvue-close:before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.anibyvue-close:after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.anibyvue-close:hover:before,
.anibyvue-close:hover:after {
    background: #333;
}
/* -- fade -- */
@-webkit-keyframes anibyvue-fade-enter {
from {
        opacity: 0;
}
}
@keyframes anibyvue-fade-enter {
from {
        opacity: 0;
}
}
.anibyvue-fade-enter-active {
    -webkit-animation: anibyvue-fade-enter both ease-in;
    animation: anibyvue-fade-enter both ease-in;
}
@-webkit-keyframes anibyvue-fade-leave {
to {
        opacity: 0
}
}
@keyframes anibyvue-fade-leave {
to {
        opacity: 0
}
}
.anibyvue-fade-leave-active {
    -webkit-animation: anibyvue-fade-leave both ease-out;
    animation: anibyvue-fade-leave both ease-out;
}
/* -- door -- */
@-webkit-keyframes anibyvue-door-enter {
from {
        -webkit-transform: scale3d(0, 1, 1);
        transform: scale3d(0, 1, 1);
}
}
@keyframes anibyvue-door-enter {
from {
        -webkit-transform: scale3d(0, 1, 1);
        transform: scale3d(0, 1, 1);
}
}
.anibyvue-door-enter-active {
    -webkit-animation: anibyvue-door-enter both cubic-bezier(0.4, 0, 0, 1.5);
    animation: anibyvue-door-enter both cubic-bezier(0.4, 0, 0, 1.5);
}
@-webkit-keyframes anibyvue-door-leave {
60% {
        -webkit-transform: scale3d(.01, 1, 1);
        transform: scale3d(.01, 1, 1);
}
to {
        -webkit-transform: scale3d(0, 1, .1);
        transform: scale3d(0, 1, .1);
}
}
@keyframes anibyvue-door-leave {
60% {
        -webkit-transform: scale3d(.01, 1, 1);
        transform: scale3d(.01, 1, 1);
}
to {
        -webkit-transform: scale3d(0, 1, .1);
        transform: scale3d(0, 1, .1);
}
}
.anibyvue-door-leave-active {
    -webkit-animation: anibyvue-door-leave both;
    animation: anibyvue-door-leave both;
}
/* -- flip -- */
@-webkit-keyframes anibyvue-flip-enter {
from {
        -webkit-transform: perspective(400px) rotate3d(1, 0, 0, 60deg);
        transform: perspective(400px) rotate3d(1, 0, 0, 60deg);
}
70% {
        -webkit-transform: perspective(400px) rotate3d(1, 0, 0, -15deg);
        transform: perspective(400px) rotate3d(1, 0, 0, -15deg);
}
to {
        -webkit-transform: perspective(400px);
        transform: perspective(400px);
}
}
@keyframes anibyvue-flip-enter {
from {
        -webkit-transform: perspective(400px) rotate3d(1, 0, 0, 60deg);
        transform: perspective(400px) rotate3d(1, 0, 0, 60deg);
}
70% {
        -webkit-transform: perspective(400px) rotate3d(1, 0, 0, -15deg);
        transform: perspective(400px) rotate3d(1, 0, 0, -15deg);
}
to {
        -webkit-transform: perspective(400px);
        transform: perspective(400px);
}
}
.anibyvue-flip-enter-active {
    -webkit-animation: anibyvue-flip-enter both ease-in;
    animation: anibyvue-flip-enter both ease-in;
    -webkit-backface-visibility: visible !important;
    backface-visibility: visible !important;
}
@-webkit-keyframes anibyvue-flip-leave {
from {
        -webkit-transform: perspective(400px);
        transform: perspective(400px);
}
30% {
        -webkit-transform: perspective(400px) rotate3d(1, 0, 0, -15deg);
        transform: perspective(400px) rotate3d(1, 0, 0, -15deg);
}
to {
        -webkit-transform: perspective(400px) rotate3d(1, 0, 0, 45deg);
        transform: perspective(400px) rotate3d(1, 0, 0, 45deg);
}
}
@keyframes anibyvue-flip-leave {
from {
        -webkit-transform: perspective(400px);
        transform: perspective(400px);
}
30% {
        -webkit-transform: perspective(400px) rotate3d(1, 0, 0, -15deg);
        transform: perspective(400px) rotate3d(1, 0, 0, -15deg);
}
to {
        -webkit-transform: perspective(400px) rotate3d(1, 0, 0, 45deg);
        transform: perspective(400px) rotate3d(1, 0, 0, 45deg);
}
}
.anibyvue-flip-leave-active {
    -webkit-animation: anibyvue-flip-leave both;
    animation: anibyvue-flip-leave both;
    -webkit-backface-visibility: visible !important;
    backface-visibility: visible !important;
}
