     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
  border: none;
  border-radius: 4px;
}
.btn:hover {
  -webkit-box-shadow: 0 0 50px rgba(255, 255, 255, .3);
