/* -- button -- */
.anibyvue-cancel-btn,
.anibyvue-confirm-btn {
  position: absolute;
  font: inherit;
  bottom: 16px;
  width: 70px;
  padding: 4px 15px;
  border-radius: 3px;
  -webkit-transition: background .2s;
  transition: background .2s;
  border: 1px solid #03a9f4;
}
.anibyvue-confirm-btn {
  color: #fff;
  right: 102px;
  background: #03a9f4;
}
.anibyvue-confirm-btn:hover {
  background: #0098e3;
}
.anibyvue-cancel-btn {
  color: #03a9f4;
  right: 16px;
  background: #fff;
}
.anibyvue-cancel-btn:hover {
  background: #fafafa;
}
.anibyvue-cancel-btn:focus,
.anibyvue-confirm-btn:focus {
  outline: none;
}
.anibyvue-confirm-btn:active {
  background: #0087d2;
}
.anibyvue-cancel-btn:active {
  background: #fafafa;
  -webkit-box-shadow: inset 1px 1px 1px rgba(0, 0, 0, .2), 0 0 1px transparent;
